.container {
  height: 100%;
  padding-top: 70px;
}

.map {
  height: 625px;
  position: relative;
  background: url("../../assets/images/background.svg");

  &__bullets {
    display: flex;
    position: absolute;
    left: 50%;
    bottom: 50px;
    transform: translateX(-50%);
  }
}

.bullet {
  border-radius: 50%;
  width: 10px;
  height: 10px;
  background-color: lightgray;
  &:not(:last-child) {
    margin-right: 5px;
  }
  &--active {
    background-color: gray;
  }
}

.svg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

  .approximate-time {
    position: relative;
    height: 500px;
    background-color: var(--background);

    .title {
      text-align: center;
      position: absolute;
      top: -31px;
      left: 50%;
      transform: translateX(-50%);
      background-color: var(--red);
      border-radius: 12px;
      color: white;
      padding: 10px 20px;
      min-width: 300px;
    }

    .time {
      position: absolute;
      top: 5px;
      left: 50%;
      border-radius: 12px;
      transform: translateX(-50%);
      text-align: center;
      padding: 15px 30px;
      &--red {
        background-color: #D70C0C;
      }
      &--green {
        background-color: #58A85C; 
      }
      color: white;
    }

    .route-container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      top: 58%;
      font-size: 20px;
      min-width: 160px;
      transform: translateX(-51.5%) rotate(-90deg);
    }

    .route-name {
    color: white;
    font-size: 16px;
  }

  .route-icon {
    margin-top: 3px;
    border-radius: 100%;
  }
}

.prediction {
  height: 150px;
  background-color: #9DA7F9;
  position: relative;
  .title {
    position: absolute;
    top: -40px;
    left: 50%;
    min-width: 185px;
    transform: translateX(-50%);
    font-size: 20px;
    color: white;
  }

  .container {
    padding-top: 0;
    display: flex;
    justify-content: center;
    align-items: center;

    .item {
      &:not(:last-child) {
        margin-right: 20px;
      }
      display: flex;
      flex-direction: column;
      align-items: center;

      &__time {
        color: white;
        margin-bottom: 5px;
      }
      
      &__duration {
        background: #E4E4E4;
        padding: 3px 11px;
        border-radius: 5px;
      }
    }
  }
}

.collapse {
  background-color: var(--background);;
}

.live-photos {
  display: flex;
  align-items: center;
  padding: 0 20px;
  img {
    max-width: 30px;
    max-height: 30px;
    margin-right: 15px
  }
  p {
    margin: 0;
  }
}

.panel {
  margin: 40px 0;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;

  .dashed-line {
    position: absolute;
    height: 100%;
    left: 25%;
    width: 5px;
    border: none;
    border-left: 3px dotted white;
  }

  &__item {
    display: flex;
    align-items: center;
    &:not(:last-child) {
      margin-bottom: 20px;
    }
    img {
      max-width: 150px;
      max-height: 97px;
      z-index: 2;
      margin-right: 25px;
    }
    span {
      min-width: 150px;
      max-width: 150px;
      font-weight: 400;
      color: white;
    }
  }
}

.ant-collapse-item {
  margin-top: -1px;
  padding-bottom: 1px;
  background-color: var(--background);
}

.ant-collapse-content {
  background-color: var(--light-blue) !important;
}

.diagram-svg {
  width: 100%;
  margin-top: 50px;
}

.ant-drawer-body {
  display: flex;
  align-items: center;
  justify-content: center;
  .button-checkin {
    width: 120px;
    height: 120px;
    border-radius: 100%;
    background: var(--background);
  }
}